.Toolbar {
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color:rgb(230, 253, 250);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    box-sizing: border-box;
    z-index: 90;
    color: black;
    border-bottom: solid lightgray 1px;
}

.Toolbar nav {
    height: 100%;
}

.Logo {
    height: 80%
}

@media (max-width: 500px){
    .DesktopOnly {
        display: none;
        
    }
}

.DesktopOnly {
    color: orange;
}