
input[type=text], select {
    width: 50%;
    padding: 12px 20px;
    margin: 8px 26px;
    display: block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    
  }
  input[type=email], select {
    width: 50%;
    padding: 12px 20px;
    margin: 8px 26px;
    display: block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  input[type=submit] {
    width: 50%;
    background-color: lightskyblue;
    color: white;
    padding: 12px 20px;
    margin: 8px 26px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    display: block;
    font-size: medium;
  }
  
  input[type=submit]:hover {
    background-color: skyblue;

 
  }
  textarea {
    width: 50%;
    display: block;
    min-height: 100px;
    border-radius: 4px;
    margin: 8px 20px;
  }
  
  div.container {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
    width: 100%;
  }

  form {
    display: flex;
    width: 100%;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
  }

  @media(max-width: 795px){
    input[type=text], select {
      width: 90%;
  
      
    }
    input[type=email], select {
      width: 90%;
      
    }
    
    input[type=submit] {
      width: 90%;
     
    }
    
    
    textarea {
      width: 90%;
    
    }
    
    div.container {
    
      padding: 0;
     
    }
  
  
  }